import React from 'react'

import { Box, Flex, Button } from 'rebass'
import { Label, Input, Textarea } from '@rebass/forms'

import { Paragraph } from '../components/Type'

class ContactForm extends React.Component {
    constructor(props) {
        super(props);
        this.submitForm = this.submitForm.bind(this)
        this.state = {
            status: ""
        }
    }

    render() {
        const { status } = this.state

        return (
            <Box
                as='form'
                mb={4}
                onSubmit={this.submitForm}
                action="https://formspree.io/mwglrbwm" method="POST"
            >
                <Label htmlFor='name'>Name:</Label>
                <Input id='name' name='name' placeholder='Nikolai'></Input>
                <Label htmlFor='email'>Email:</Label>
                <Input id='email' name='email' type='email' placeholder='nikolai@theministry.co'></Input>
                <Label htmlFor='message'>Message:</Label>
                <Textarea placeholder="Let's discuss something cool!" name='message'>
                </Textarea>
                <Flex flexDirection='row-reverse' mt={3}>
                    { status === "SUCCESS" ? <Paragraph>Thanks! Your message has been sent.</Paragraph> : <Button type='submit' variant='outline' ml={3}>Send</Button> }
                    { status === "ERROR" && <Paragraph>Something went wrong. Try again or <a href='mailto:nikolai@theministry.co'>send an email</a></Paragraph> }
                </Flex>
            </Box>
        )
    }

    submitForm(e) {
        e.preventDefault()
        const form = e.target;
        const data = new FormData(form);
        const xhr = new XMLHttpRequest();
        xhr.open(form.method, form.action);
        xhr.setRequestHeader("Accept", "application/json");
        xhr.onreadystatechange = () => {
        if (xhr.readyState !== XMLHttpRequest.DONE) return;
        if (xhr.status === 200) {
            form.reset();
            this.setState({ status: "SUCCESS" });
        } else {
            this.setState({ status: "ERROR" });
        }
        };
        xhr.send(data);
    }
}

export default ContactForm
